.contact-element{
    border-bottom: 1px solid #e8e8e8;
    margin-left: 16px;
    margin-right: 16px;
}

.contacts-item {
    padding-top   : 8px;
    padding-bottom: 8px;
}

.contacts-item:nth-child(2) {
    border: none;
}

.contacts-item-content {
    padding: 10px 20px 0 10px;
    color    : #7b7b7b;
}


.contacts-item:hover .contacts-item-content {
    color: #4e4e4e;
}

#name {
    font-weight: 600;
    font-size: 15px;
}

#number {
    font-size: 14px;
    margin-top: -5px;
}
#address{
    margin-top: -5px;
}
.contacts-initials {
    width         : 45px;
    height        : 45px;
    background    : #ecb2c3;
    border-radius : 50%;
    color         : white;
    cursor    : pointer;
}
.contacts-initials span{
    margin: auto;
    padding-bottom: 3px;
}

.fa-trash {
    display            : inline-block;
    width              : 19px;
    height             : 22px;
    font-size: 22px;
    color: #dadada;
    flex-shrink: 0;
    cursor    : pointer;
}

.fa-trash:hover {
    color: #b6b5b5;
}
.show:hover{
    color: #dadada;
    cursor: auto;
}

@media (min-width: 414px){
    #number{
        align-self: flex-end;
    }
}

.hide{
    display: none;
}

.reminder{
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
    padding: 8px;
    border: 1px solid grey;
    border-radius: 6px;
}

.reminder .fa{
    padding: 4px;
    color: #888787;
}
.reminder .fa-times{
    border-radius: 4px;
    padding: 6px;
}
.reminder .fa-times:hover{
    background-color: #e6e6e6;
}

.reminder>div:first-child{
    overflow-y: scroll;
    scrollbar-width: thin;
    scrollbar-color: #e6e6e6 transparent;
}

.reminder>div:nth-child(2){
    margin-top: 8px;
    margin-bottom: 12px;
    overflow-y: scroll;
    scrollbar-width: thin;
    scrollbar-color: #e6e6e6 transparent;
    max-height: 120px;
}

textarea{
    overflow-y: scroll;
    scrollbar-width: thin;
    scrollbar-color: #e6e6e6 transparent;
}

::-webkit-scrollbar{
    width: 1px;
}

::-webkit-scrollbar-track{
    background-color: transparent;
}
