footer{
    overflow: hidden;
    border-bottom-left-radius : 10px;
    border-bottom-right-radius: 10px;
    flex-shrink               : 0;
}


footer>div{
    padding         : 8px 4px;
    background-color: #f5f5f5;
    overflow-y: scroll;
    scrollbar-width: thin;
    scrollbar-color: transparent transparent;
    font-size: 14px;
}

.main-container{
    padding-right: 8px;
}

.content {    
    flex-grow: 1;    
    flex-direction: column;
}

.input-icons { 
    display: flex;
    padding-left: 12px;
    color: grey;
} 
.warning{
    color: #dc3545;
}


.input-field {
    border : none;
    border-radius: 4px;
    background: transparent;    
    box-sizing: border-box;
    width: 0;
}

.input-field:focus,
.input-field:active,
.input-field:hover {
    border : none;
    outline: none;
}

.content div:first-child{
    flex-basis: 20%;
}
.icon{
    align-self: center;
}


#add-btn {
    padding      : 6px;
    border-radius: 6px;
    font-weight  : bold;
    cursor       : pointer;
    color        : grey;
    font-size: 16px;
}

#add-btn:hover {
    background-color: #dddddd;
}

@media (min-width: 414px){
    .content{
        flex-direction: row;
        margin-bottom: 8px;
    }
    .input-field{
        background-color: #ecebeb;
    }
}
