.items-container {
    flex-grow     : 1;
    display       : flex;
    flex-direction: column;
    overflow-y: scroll;
    scrollbar-width: thin;
    scrollbar-color: #e6e6e6 transparent;
    position: relative;
}

.items-container .no-items-message {
    display: none;
    margin : auto;
}

.items-container .no-items-message:only-child {
    display: block;
}
