header{
    overflow: hidden;
    border-top-left-radius : 10px;
    border-top-right-radius: 10px;
    box-sizing: border-box;
    flex-shrink: 0;
}



#header {
    height : 85px;
    color: grey;
    background: #f5f5f5;
    font-size: 24px;
    position: relative;
    overflow-y: scroll;
    scrollbar-width: thin;
    scrollbar-color: transparent transparent;

}

#search-btn {
    cursor: pointer;    
    padding: 6px;    
    position: absolute;
    right: 7px;
    border-radius: 6px;
}

#search-btn::before {
    color: lightgrey;
}

#search-btn:hover {
    background-color: #eae9e9;
}


::-webkit-scrollbar {
    width: 5px;
}

::-webkit-scrollbar-track-piece {
    background-color: #FFF;
}

::-webkit-scrollbar-thumb {
    background-color: #c7c7c7;
}

::-webkit-scrollbar-thumb:hover {
    background-color: #909090;
}


.search {
    display: flex;
    padding: 0 12px 12px 16px;
    background-color: #f5f5f5;
    overflow-y: scroll;
    scrollbar-width: thin;
    scrollbar-color: transparent transparent;
    font-size: 14px;
}
.hide {
    display: none;
}

#search-field {
    flex-grow: 1;
    border: none;
    background-color: white;
    border-radius: 4px;

}

#search-field:focus,
#search-field:active,
#search-field:hover {
    border : none;
    outline: none;
}


button {
    border: none;
    border-radius: 4px;
    width: 20%;
    background-color: #eae9e9;
    cursor: pointer;
    color: grey;
    height: 26px;
}

button:active {
    border: none;
    outline: none;
}

@media (min-width: 414px){
    #header{
        height: 70px;
    }
}